.page {
    width: 1200px;
    height: auto;

    font-family: "OpenSans", "Arial", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;

    background-color: @background-page;

    padding: 0;
    margin: 0 auto;
}

.visually-hidden {
    position: absolute;
  
    width: 1px;
    height: 1px;
  
    border: 0;
  
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  
    padding: 0;
    margin: -1px;
  }
  