.photo-gallery {
    padding: 50px 50px;

    background-color: @background-education;
}

.photo-gallery__title {
    margin: 0 0 50px;
}

.photo-gallery__figcaption {
    text-align: center;

    margin-bottom: 10px;
}

.photo-gallery__container {
    width: 800px;
    height: 800px;

    display: flex;
    flex-flow: row wrap;

    background-color: @background-counter;

    margin: 0 auto;
}

.photo-gallery__picture-1,
.photo-gallery__image-1,
.photo-gallery__picture-3,
.photo-gallery__image-3 {
    width: 350px;
    height: 400px;
}

.photo-gallery__picture-2,
.photo-gallery__image-2,
.photo-gallery__picture-4,
.photo-gallery__image-4 {
    width: 450px;
    height: 400px;
}