@keyframes shake {
    0%, 50%, 100% {
        transform: rotate(0deg);
    }
    10%, 30% {
        transform: rotate(-10deg);
    }
    20%, 40% {
        transform: rotate(10deg);
    }
}

.social__container {
    display: flex;
    justify-content: center;
}

.social__link {
    width: 40px;
    height: 40px;
}

.social__vk,
.social__instagram {
    width: 40px;
    height: 40px;
}

.social__vk path,
.social__instagram path {
    width: 40px;
    height: 40px;
}

/*.social__vk path:hover,
.social__instagram path:hover {
    animation: shake 2s ease-in-out
}*/

.social__vk:hover,
.social__instagram:hover {
    animation: shake 1.2s ease-in-out
}
