.portfolio {
    background-color: @background-education;
    padding: 50px 50px;
}

.portfolio__title {
    margin: 0 0 30px
}

.portfolio__text {
    margin: 0 0 20px;
}

.portfolio__list {
    .reset-list;

    display: flex;
    flex-flow: row wrap;
}

.portfolio__link {
    text-decoration: none;

    color: @color-portfolio;
}

.portfolio__link:hover {
    color: @color-portfolio-hover;
}

.portfolio__item-description {
    margin-bottom: 10px;
}

.portfolio__item-description:last-child {
    margin-bottom: 0;
}